import React from 'react';
import '../Styles/Base/Base.css';




import Lists from '../components/Lists';
import MainCard from '../components/MainCard';
import UpComEvents from '../components/UpComEvents';
import Seats from '../components/Seats';
import Sales from '../components/Sales';
import Online from '../components/Online';


function Dashboard() {
    return (
        <>
            

            <div class="container mt-4">
                <div class="row mt-4">
                    <div class="col-lg-6">
                        <Sales />
                    </div>
                    <div class="col-lg-3">
                <MainCard />
                    </div>
                    <div class="col-lg-3">
                        <Lists />
                    </div>
                </div>

                <div class="row mt-4">
                    <div class="col-lg-6">
                        <UpComEvents />
                    </div>
                    <div class="col-lg-3">
                    <Seats />
                    </div>
                    <div class="col-lg-3">
                    <Online />
                    </div>
                </div>
            </div>


        </>
    );
}

export default Dashboard;