import React, { useState } from "react";
import ReactApexChart from "react-apexcharts";
import '../Styles/components/Online/Online.css'

function Online() {
  const [state, setState] = useState({
    series: [
      {
        name: "Desktops",
        data: [10, 41, 35, 51, 49, 62, 69, 91, 148],
      },
    ],
    options: {
        colors : ['#e53632', '#e53632'],
      chart: {
        height: 350,
        type: "line",
        zoom: {
          enabled: false,
        },
      },
      dataLabels: {
        enabled: false,
        
      },
      stroke: {
        curve: "smooth",
      },


      xaxis: {
        show: false,
        labels: {
          show: false,
        },
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
      },
      markers: {
        size: [4, 7],
      },

      yaxis: {
        show: false,
        labels: {
          show: false,
        },
        axisBorder: {
          show: false,
        },
        axisTicks: {
          show: false,
        },
      },
    },
  });

  return (
    <>
    
      <div className="OnlineMain">
        <div className="Online-Card1">
          <div className="Online-Card1-body">
            <h4 className="Online-Card1-body--heading">ONLINE SELLS</h4>
            <div className="row">
              <div className="col">
                <h6>Rough Cost</h6>
                <h4 className="OnlinePrimary">$5500.00</h4>
              </div>
            </div>
            <div>
              <ReactApexChart
                options={state.options}
                series={state.series}
                type="line"
                height={110}
              />
            </div>
          </div>
        </div>
        <div className="Online-Card2">
            <h4>EMAIL COMPAIGN</h4>
            <div className="campaign-progress">
                <p className="m-t-15 m-b-5">Send Emails <span class="pull-right">+165</span></p>
                <div className="progress">
  <div className="progress-bar" role="progressbar" style={{width: "85%"}} aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
</div>
<p className="m-t-15 m-b-5">Clicks <span class="pull-right">+355</span></p>
                <div className="progress">
  <div className="progress-bar" role="progressbar" style={{width: "85%"}} aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
</div>
            </div>
        </div>
      </div>
    </>
  );
}

export default Online;
