import React from 'react'
import '../Styles/components/Seats/Seats.css'

function Seats() {

  return (
    <>
      <div className='card-body'>
      <h4>Total Seats</h4>

      <div className="ticketContainer">


        <div className="ticketContainer_circle">
          <div className="ticketContainer_circle-main">
          <canvas id='myCanvas' width="230" height="130"></canvas>
          </div>
          <div className="ticketContainer_circle-text">
            <h2>365</h2>
            <span>Total Seats</span>
          </div>
        </div>

        <div className="ticketContainer_details">
          <div className="ticketContainer_details-seat1">
            <h6>Total Seats</h6>
            <h4>$2000.00</h4>
          </div>
          <div className="ticketContainer_details-seat2">
            <h6>Total Seats</h6>
            <h4>$2000.00</h4>
          </div>
        </div>

<div>
  <h5 className='m-t-30 m-b-15'>Sold Seats 
  <span className='pull-right'>95</span>
  </h5>
<div className="progress">
  <div className="progress-bar" role="progressbar" style={{width: "75%"}} aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
</div>
</div>    
      </div>
      </div>
    </>
  )
}

export default Seats;